.article {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    width: 250px;
    border-radius: .5rem;
    box-shadow: 1px -.5px 1px 2px #d1d1d1;
    transition: all .5s;

    .image {
        .img-content {
            height: 200px;
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            img {
                max-width: 100%;
                max-height: 180px;
                object-fit: cover;
            }
        }

        .img-content::after {
            transition: all .3s;
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-color: inherit;
            max-width: 100%;
            max-height: 100%;
            border-top-left-radius: .5rem;
            border-top-right-radius: .5rem;
        }
    }

    .image:hover {
        .img-content::after {
            background-color: #001f4d2a;
        }

        .img-content {
            .img {
                max-height: 200px !important;
            }
        }
    }

    .descriptions {
        .article_name {
            padding: 7px 0;
            background-color: #f3f3f3;
            font-weight: bolder;
            font-weight: 400;
        }

        .price-detail {
            padding: 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .price {
                font-size: 18px;
                font-weight: 500;
                color: #000000;
            }
        }
    }
}
