/* news section style */
.news_section .news_container .news_content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.articles_slider {
  justify-content: center;
  align-items: center;
}

/* accessoitres pour enfants */
.accessoires-enfant_section .accessoires_container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.accessoires-enfant_section .accessoires_container .accessoires_content {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 50px;
}

/* asides section */
.asides .asides_container {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

/* wrappers */
.wrapper-section .wrapper_section_content {
  display: flex;
  flex-direction: column;
  gap: 30px;
}/*# sourceMappingURL=articles-section-style.css.map */